              "生产商品的劳动的二重性：商品是通过人的劳动生产出来的，商品的使用价值和价值二因素是由生产商品的劳动的二重性所决定的。这种劳动的二重性就是具体劳动和抽象劳动。1、具体劳动——生产商品劳动的自然属性指生产一定使用价值，具有特定性质、目的和形式的劳动。具体劳动创造出使用价值，不同的具体劳动创造出不同的使用价值。2、抽象劳动——生产商品劳动的社会属性指撇开一切具体形式的、无差别的一般人类劳动。商品的价值是无差别的一般人类劳动的凝结，也就是抽象劳动的凝结。抽象劳动是商品价值的实体。所以，生产商品的抽象劳动创造了商品的价值。",
